@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 40px;
  background:blueviolet;
  z-index: 950;
  
}

.list {
  list-style-type: none;
  background: blueviolet;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.items {
  margin-right: 20px;
  font-size: 20px;
  text-transform: uppercase;
  color: #f1f1f1;
  cursor: pointer;
}

.btn {
  display: none;
  position: absolute;
  right: 10px;
  bottom: 7px;
  padding: 5px;
  color: #000;
  font-size: 18px;
}


@media screen and (max-width: 500px){

  .list {
      flex-direction: column;
      height: auto;
  }
  .items:nth-child(1){
      border-top: 1px solid rgba(255, 255, 255, 0.555);
      margin-top: 50px;
  }
  .items {
      width: 100%;
      border-top: 1px solid rgba(255, 255, 255, 0.555);
      text-align: center;
      margin-right: 0px;
      padding: 20px 0;
  }
  .btn {
      display: block;
  }

}